import React, { Fragment, useState, useRef, useEffect } from "react";
import api from "axios";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Table from "components/Table/Table.js";
import Button from "components/CustomButtons/Button.js";
import Danger from "components/Typography/Danger.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import styles from "assets/jss/material-kit-pro-react/views/dashboardStyle";
import { makeStyles, Tooltip } from "@material-ui/core";
import { ArtTrack, Edit, Place, Refresh } from "@material-ui/icons";
import { useSelector } from "react-redux";


const LinkPreview = ({ url }) => {
    const useStyles = makeStyles(styles);
    const classes = useStyles();
    const { messagefontSize } = useSelector(state => state.login);
    const [info, setInfo] = useState(undefined)


    useEffect(() => {
        const gcp_link_preview_api_url = "http://104.198.66.120:5000";
        const parameters = { url: url };

        api.post(gcp_link_preview_api_url, parameters)
            .then((response) => {
                if (response && response.data) {
                    const { data } = response;
                    setInfo(data);
                }
            })
            .catch((error) => {
                console.log(error);
            })
    }, [])

    const chatFontSize = {
        fontSize: messagefontSize ? messagefontSize : "0.7rem",
        wordBreak: "break-word",
    }

    const onClick = (e, url) => {
        e.preventDefault();
        window.open(url);
    }

    if (info) {
        return (
            <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                    <Card product className={classes.cardHover}>
                        <CardHeader image className={classes.cardHeaderHover}>
                            <a href={url} onClick={(e) => { onClick(e, url) }}>
                                <img src={info.image} alt="..." />
                            </a>
                        </CardHeader>
                        <CardBody>
                            <div className={classes.cardHoverUnder}>
                            </div>
                            <h4 className={classes.cardProductTitle}>
                                <a href={url} style={chatFontSize} onClick={(e) => { onClick(e, url) }} >
                                    {info.title}
                                </a>
                            </h4>
                            <p className={classes.cardProductDesciprion} style={chatFontSize} onClick={(e) => { onClick(e, url) }}>
                                {info.description}
                            </p>
                        </CardBody>
                    </Card>
                </GridItem>
            </GridContainer>
        )
    } else {
        return (<>
            <a href={url} target="_blank">{url}</a>
        </>)
    }



}

export default LinkPreview;